<template>
    <div>
        <v-row>
            <v-col v-for="item in blogArticles" :key="item.id">
                <v-card @click="clickArticle(item)" hover class="mx-auto"
                        max-width="800px">
                    <v-img class="white--text align-end" :max-height="$vuetify.breakpoint.smAndDown?'200px':'300px'"
                           src="https://cdn.vuetifyjs.com/images/cards/docks.jpg">
                        <v-card-title>{{item.title}}</v-card-title>
                    </v-img>
                    <v-card-text class="text--primary">{{item.abstract}}</v-card-text>
                    <v-divider/>
                    <v-col>
                        <v-icon small>mdi-eye-outline</v-icon>
                        {{item.id}}
                        <v-icon small class="ml-2">mdi-chat-outline</v-icon>
                        {{item.id}}
                        <v-icon small class="ml-2">mdi-clock-time-four-outline</v-icon>
                        {{item.timeString}}
                    </v-col>
                </v-card>
            </v-col>
        </v-row>
        <div class="text-center">
            <v-pagination v-if="blogArticles.length>=0" v-model="page" :length="totalPage"/>
        </div>
    </div>
</template>

<script>
    export default {
        name: "ArticlePreview",
        props: ['blogArticles', "totalPage"],
        data() {
            return {
                page: 0
            }
        },
        methods: {
            clickArticle: function (blogArticle) {
                //path不能和params连用
                this.$router.push({
                    name: "blogArticle",
                    params: {id: blogArticle.id, title: blogArticle.title}
                })
            }
        },
        watch: {
            page: function () {
                console.log(this.page)
            }
        },
        created: function () {
            let length = this.blogArticles.length;
            if (length !== 0) {
                this.page = 1
            }
        }
    }
</script>

