<template>
    <v-container fluid>
        <v-card class="pa-2">
            <div class="d-flex justify-space-between">
                <v-card-title class="pt-0 pb-0 ps-2">分类</v-card-title>
                <v-card-title class="pt-0 pb-0 pe-2">
                    共<span class="display-1 d-inline primary--text">{{types.length}}</span>个
                </v-card-title>
            </div>
            <v-chip-group column multiple active-class="primary--text" v-model="selectedTypes">
                <v-chip filter v-for="tag in types" :key="tag">{{tag}}</v-chip>
            </v-chip-group>
        </v-card>
        <ArticlePreview :blogArticles="blogArticles" :total-page="2"/>
    </v-container>
</template>

<script>
    import ArticlePreview from "@/components/ArticlePreview";

    export default {
        name: "Type",
        components: {ArticlePreview},
        props: ["paramType"],
        data: () => ({
            types: [
                'Work',
                'Home Improvement',
                'Vacation',
                'Food',
                'Drawers',
                'Shopping',
                'Art',
                'Tech',
                'Creative Writing'
            ],
            selectedTypes: [],
            blogArticles: [
                {
                    id: 1,
                    title: "标题1",
                    abstract: "内容1",
                    timeString: "2020/05/17 21:36"
                },
                {
                    id: 2,
                    title: "标题2",
                    abstract: "内容2",
                    timeString: "2020/05/17 21:36"
                },
                {
                    id: 3,
                    title: "标题3",
                    abstract: "内容3",
                    timeString: "2020/05/17 21:36"
                }
            ]
        }),
        mounted() {
        },
        watch: {
            selectedTypes() {
                //TODO 根据分类查询文章
                console.log(this.selectedTypes)
            },
            paramType() {
                console.log("paramType", this.paramType)
                if (this.paramType !== undefined) {
                    for (let i = 0; i < this.types.length; i++) {
                        if (this.types[i] === this.paramType) {
                            this.selectedTypes = []
                            this.selectedTypes.push(i)
                            break
                        }
                    }
                }
            }
        }
    }
</script>

